@import "../../style/colors";

.container h2 {
  text-align: center;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.filmContainer {
  display: flex;
  max-width: 600px;
  margin-top: 20px;
}

.filmImgContainer {
  display: inline-block;
  border: solid 0px grey;
  height: 200px;
  border-radius: 10px;
  overflow: hidden;
  margin-right: 20px;
  flex-shrink: 0;
  position: relative;
}

.filmImg{
  height: 200px;
}

.ribbon {
  position: absolute;
  z-index: 1;
  top: -30px;
  left: 0;
  width: 100%;
}

.filmInfoContianer {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.filmTitle {
  margin: 0;
}

.filmPlot {

}

.button {
  background-color: $primaryDark !important;
}

.button:hover {
  background-color: $primaryDark !important;
  filter: brightness(80%);
}

.button::before {
  content: '';
  display: block;
  position: absolute;
  top: -7px;
  left: -3px;
  right: 0;
  height: 23px;
  background: url(data:image/png;base64,) no-repeat 0 0,
  url(data:image/png;base64,) no-repeat 50% 0,
  url(data:image/png;base64,) no-repeat 100% 0;
}